const actions = {
  getCashManagements: (page, search, filter) => ({
    type: 'GET_CASH_MANAGEMENTS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getImportCashManagements: (page, search, filter) => ({
    type: 'GET_IMPORT_CASH_MANAGEMENTS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createCashManagement: () => ({
    type: 'CREATE_CASH_MANAGEMENT_REQUEST'
  }),
  storeCashManagement: data => ({
    type: 'STORE_CASH_MANAGEMENT_REQUEST',
    payload: { body: data }
  }),
  editCashManagement: id_cash_management => ({
    type: 'EDIT_CASH_MANAGEMENT_REQUEST',
    payload: { id_cash_management: id_cash_management }
  }),
  updateCashManagement: (id_cash_management, data) => ({
    type: 'UPDATE_CASH_MANAGEMENT_REQUEST',
    payload: {
      id_cash_management: id_cash_management,
      body: data
    }
  }),
  showCashManagement: id_cash_management => ({
    type: 'SHOW_CASH_MANAGEMENT_REQUEST',
    payload: { id_cash_management: id_cash_management }
  }),
  processCashManagement: id_cash_management => ({
    type: "PROCESS_CASH_MANAGEMENT_REQUEST",
    payload: { id_cash_management }
  }),
  cancelCashManagement: (id_cash_management, data) => ({
    type: "CANCEL_CASH_MANAGEMENT_REQUEST",
    payload: { 
      id_cash_management,
      body : data
    }
  }),
  uploadDocument: (id_cash_management, data) => ({
    type: "UPLOAD_CASH_MANAGEMENT_DOCUMENT_REQUEST",
    payload: {
      id_cash_management,
      body : data
    }
  }),
  downloadDocument: id_cash_management_document => ({
    type: "DOWNLOAD_CASH_MANAGEMENT_DOCUMENT_REQUEST",
    payload: {id_cash_management_document}
  }),
  deleteDocument: id_cash_management_document => ({
    type: "DELETE_CASH_MANAGEMENT_DOCUMENT_REQUEST",
    payload: { id_cash_management_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
