import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import CardWrapper from "../../../App/app.style";
import ModalDetail from "./modalDetail";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let totalAmount = 0;
  let totalCurrencyAmount = 0;
  arr.map(data => {
    totalAmount += parseFloat(data.amount);
    totalCurrencyAmount += parseFloat(data.currency_amount);
  });

  return {
    totalAmount: totalAmount,
    totalCurrencyAmount: totalCurrencyAmount
  };
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { cash_management_journals } = props;

    this.state = {
      dataSource: [...cash_management_journals],
      count: cash_management_journals.length + 1,
      totalAmount: 0,
      totalCurrencyAmount: 0,
      visible: false,
      selectedItem: {}
    };
  }

  componentDidMount() {
    const {
      temp_cash_management_journal,
      temp_cash_management_journals,
      clearCashManagementJournal,
      clearCashManagementJournals,
      id_location,
      locations,
      cash_flow_types,
    } = this.props;

    const [location] = locations.filter(
      currData => currData.id_location === id_location
    );

    // milih expense sendiri
    if (temp_cash_management_journal) {
      const {
        dataSource,
        count,
        totalAmount,
        totalCurrencyAmount,
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === temp_cash_management_journal.id_account
      );

      // account baru
      if (find < 0) {

        let deposit = cash_flow_types.find((x) => x.default_cash_management_deposit === 1)
          ? cash_flow_types.find((x) => x.default_cash_management_deposit === 1)
          : cash_flow_types[0]
        let withdrawal = cash_flow_types.find((x) => x.default_cash_management_withdrawal === 1)
          ? cash_flow_types.find((x) => x.default_cash_management_withdrawal === 1)
          : cash_flow_types[0]


        const init = {
          index_no: count,
          amount: 0,
          currency_amount: 0,
          id_cash_flow_type: deposit.transaction_type === 1 ? deposit.id_cash_flow_type : withdrawal.id_cash_flow_type,
          description: deposit.transaction_type === 1 ? deposit.description : withdrawal.description,
          id_location: location.id_location,
          location_name: location.location_name,
          id_department: null,
          id_project: null
        };

        const newData = Object.assign({}, temp_cash_management_journal, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalAmount: totalAmount,
          totalCurrencyAmount: totalCurrencyAmount
        });

        clearCashManagementJournal(
          [...dataSource, newData],
          totalCurrencyAmount,
          totalAmount
        );
        // console.log(this.props.expense);
      }
    }
    // jika temp dari transaksi sebelumnya
    else if (temp_cash_management_journals.length !== 0) {
      let newTotAmount = 0;
      let newTotCurrAmount = 0;
      let newIndex = 0;

      temp_cash_management_journals.map(currData => {
        newTotAmount += parseFloat(currData.amount);
        newTotCurrAmount += parseFloat(currData.currency_amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_cash_management_journals],
        totalAmount: newTotAmount,
        totalCurrencyAmount: newTotCurrAmount,
        count: newIndex
      });
      clearCashManagementJournals(temp_cash_management_journals, newTotCurrAmount, newTotAmount);
    }
  }

  componentDidUpdate(prevProps) {
    const {
      temp_cash_management_journal,
      temp_cash_management_journals,
      clearCashManagementJournal,
      clearCashManagementJournals,
      id_location,
      locations,
      cash_flow_types
    } = this.props;

    const [location] = locations.filter(
      currData => currData.id_location === id_location
    );

    // milih expense sendiri
    if (temp_cash_management_journal) {
      const {
        dataSource,
        count,
        totalAmount,
        totalCurrencyAmount
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === temp_cash_management_journal.id_account
      );

      // account baru
      if (find < 0) {
        let deposit = cash_flow_types.find((x) => x.default_cash_management_deposit === 1)
          ? cash_flow_types.find((x) => x.default_cash_management_deposit === 1)
          : cash_flow_types[0]
        let withdrawal = cash_flow_types.find((x) => x.default_cash_management_withdrawal === 1)
          ? cash_flow_types.find((x) => x.default_cash_management_withdrawal === 1)
          : cash_flow_types[0]

        const init = {
          index_no: count,
          amount: 0,
          currency_amount: 0,
          id_cash_flow_type: deposit.transaction_type === 1 ? deposit.id_cash_flow_type : withdrawal.id_cash_flow_type,
          description: deposit.transaction_type === 1 ? deposit.description : withdrawal.description,
          id_location: location.id_location,
          location_name: location.location_name,
          id_department: null,
          id_project: null
        };

        const newData = Object.assign({}, temp_cash_management_journal, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalAmount: totalAmount,
          totalCurrencyAmount: totalCurrencyAmount
        });

        // show handle Detail untuk pilih sub_ledger_name
        if (temp_cash_management_journal.sub_ledger_type === 1 || temp_cash_management_journal.sub_ledger_type === 2) {
          this.handleDetailModal(newData);
        }

        clearCashManagementJournal(
          [...dataSource, newData],
          totalCurrencyAmount,
          totalAmount,
        );
        // console.log(this.props.expense);
      }
    }
    // jika expense dari transaksi sebelumnya
    else if (temp_cash_management_journals.length !== 0) {
      let newTotAmount = 0;
      let newTotCurrAmount = 0;
      let newIndex = 0;

      temp_cash_management_journals.map(currData => {
        newTotAmount += parseFloat(currData.amount);
        newTotCurrAmount += parseFloat(currData.currency_amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_cash_management_journals],
        totalAmount: newTotAmount,
        totalCurrencyAmount: newTotCurrAmount,
        count: newIndex
      });
      clearCashManagementJournals(temp_cash_management_journals, newTotCurrAmount, newTotAmount);
    }

    if (this.props.clearCashManagementJournalsState) {
      let newTotAmount = 0;
      let newTotCurrAmount = 0;
      let newIndex = 0;

      this.setState({
        dataSource: [],
        totalAmount: newTotAmount,
        totalCurrencyAmount: newTotCurrAmount,
        count: newIndex
      });
      clearCashManagementJournals(temp_cash_management_journals, newTotCurrAmount, newTotAmount);
    }
    // else if(prevProps.currency_rate !== this.props.currency_rate ){
    //   this.props.cash_management_journals.map(())
    // }
  }

  handleDetailModal = row => {
    const { visible } = this.state;
    // console.log(row);

    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSave } = this.props;
    let newTotAmount = 0;
    let newTotCurrAmount = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotAmount += parseFloat(currData.amount);
      newTotCurrAmount += parseFloat(currData.currency_amount);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalAmount: newTotAmount,
      totalCurrencyAmount: newTotCurrAmount
    });
    handleSave(newData, newTotCurrAmount, newTotAmount);
  };

  handleSave = row => {
    const { currency_rate } = this.props;
    const newData = this.state.dataSource;
    const rate = currency_rate ? parseFloat(currency_rate) : 1;
    let newTotAmount = 0;
    let newTotCurrAmount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    //console.log(index);

    row.amount = parseFloat(row.currency_amount) * rate;

    // console.log(newData);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    // console.log(newData)

    newData.map(currData => {
      newTotAmount += parseFloat(currData.amount);
      newTotCurrAmount += parseFloat(currData.currency_amount);
    });


    this.setState({
      dataSource: [...newData],
      totalAmount: newTotAmount,
      totalCurrencyAmount: newTotCurrAmount
    });
    this.props.handleSave(newData, newTotCurrAmount, newTotAmount);
  };

  render() {

    let columns = [
      {
        title: getLanguage({id: "no."}),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: (text, row) => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 3
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({id: "accountCode"}),
        dataIndex: "account_code",
        width: "20%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "accountName"}),
        dataIndex: "account_name",
        width: "35%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "amount"}),
        dataIndex: "currency_amount",
        width: "15%",
        align: "right",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              record,
              dataIndex: "currency_amount",
              title: "Amount",
              width: "15%",
              status: this.props.status
            };
          } else {
            return {
              record,
              editable: true,
              dataIndex: "currency_amount",
              title: "Amount",
              width: "15%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      {
        title: `${getLanguage({id: "amount"})} (IDR)`,
        width: "15%",
        dataIndex: "amount",
        align: "right",
        render: (text, row) => {
          const val = <NumberFormat value={text} />;
          if (isNaN(row.index_no)) {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          } else {
            return val;
          }
        }
      },
      {
        title: getLanguage({id: "sidebar.cashFlowType"}),
        dataIndex: "description",
        width: "30%",
        align: "center",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              colSpan: 2
            };
          } else {
            return {
              record,
              editable: true,
              cash_flow_types: this.props.cash_flow_types,
              locations: this.props.locations,
              dataIndex: "description",
              title: "Cash Flow Type",
              width: "30%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      // {
      //   title: "Location",
      //   dataIndex: "location_name",
      //   width: "20%",
      //   align: "center",
      //   editable: true,
      //   onCell: record => {
      //     if (isNaN(record.index_no)) {
      //       return {
      //         colSpan: 0
      //       };
      //     } else {
      //       return {
      //         record,
      //         editable: true,
      //         cash_flow_types: this.props.cash_flow_types,
      //         locations: this.props.locations,
      //         dataIndex: "location_name",
      //         title: "Location",
      //         width: "20%",
      //         handleSave: this.handleSave,
      //         status: this.props.status
      //       };
      //     }
      //   }
      // },
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
                // fixed: null
              }
            };
          }
          if (!this.props.status || this.props.status === 1) {
            return (
              <div>   
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="detail" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceEdtBtn"
                    size="small"
                    onClick={() => this.handleDetailModal(row)}
                  >
                    <i className="ion-navicon-round" />
                  </Button>
                </Tooltip>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  okText={getLanguage({ id: "yes" })}
                  cancelText={getLanguage({ id: "no" })}
                  onConfirm={() => this.handleDelete(row.index_no)}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>
              </div>
            );
          } else {
            return (
              <Tooltip
                placement="top"
                title={<IntlMessages id="detail" />}
              >
                <Button
                  //style={{ borderRadius: "50%" }}
                  className="invoiceEdtBtn"
                  size="small"
                  onClick={() => this.handleDetailModal(row)}
                >
                  <i className="ion-navicon-round" />
                </Button>
              </Tooltip>
            );
          }
        }
      }
    ];

    const { dataSource, visible, selectedItem } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const total = getTotal(dataSource);
    const info = {
      index_no: "Total",
      amount: <NumberFormat value={total.totalAmount} />,
      currency_amount: <NumberFormat value={total.totalCurrencyAmount} />
    };
    const modifiedData = dataSource.length > 0 ? [...dataSource, info] : [];
    // console.log(modifiedData, total);
    return (
      <CardWrapper>
        <div>
          <ModalDetail
            cash_banks={this.props.cash_banks}
            customers={this.props.customers}
            departments={this.props.departments}
            handleDelete={this.handleDelete}
            handleDetailModal={this.handleDetailModal}
            handleDetail={this.handleDetail}
            projects={this.props.projects}
            selectedData={selectedItem}
            status={this.props.status}
            submit={this.handleDetail}
            suppliers={this.props.suppliers}
            visible={visible}
          />

          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
            // scroll={{ x: 1300 }}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
