import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import {  Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {  

  render() {

    let column = [
      {
        title: getLanguage({id: "no."}),
        dataIndex: "index_no",
        align: "right",
  
      },
      {
        title: getLanguage({id: "accountCode"}),
        dataIndex: "account.account_code", 
      },
      {
        title: getLanguage({id: "accountName"}),
        dataIndex: "account.account_name",
        width: "15%",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "amount"}),
        dataIndex: "currency_amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "baseAmount"}),
        dataIndex: "amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "sidebar.cashFlowType"}),
        dataIndex: "cash_flow_type.description",
        width: "15%",
        align: "center",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          }
          else {
            return text;
          }
        }
      },
      // {
      //   title: getLanguage({id: "sidebar.location"}),
      //   dataIndex: "location.location_name",
      //   width: "15%",
      //   align: "center",
      //   render: text => {
      //     if (text && text.length > 50) {
      //       return text.substring(0, 50) + "...";
      //     } else {
      //       return text;
      //     }
      //   }
      // },
    ];

    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;
    const leftSide = [
      {
        label: <IntlMessages id="dueDate" />,
        description: moment(data.due_date, "YYYY-MM-DD").format("DD MMMM YYYY")
      },
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data.location ? data.location.location_name : null
      },
      {
        label: <IntlMessages id="cashBanks.pageHeader" />,
        description: data.cash_bank 
          ? 
            data.cash_bank.bank_account_number
              ? data.cash_bank.cash_bank_code + " (" + data.cash_bank.bank_account_number + ")"
              : data.cash_bank.cash_bank_code
          : null
      },
      {
        label: <IntlMessages id="currencies.pageHeader" />,
        description: data.currency ? data.currency.currency_code : null 
      },
      {
        label: <IntlMessages id="rate" />,
        description: data.currency_rate 
            ? <NumberFormat value={data.currency_rate} />
            : null

      },
      {
        label: <IntlMessages id="amount" />,
        description: data.currency_amount 
            ? <NumberFormat value={data.currency_amount} />
            : null
      },
      {
        label: <IntlMessages id="baseAmount" />,
        description: data.cash_flow_amount 
            ? <NumberFormat value={data.cash_flow_amount} />
            : null
      },
      
    ];

    const rightSide = [
      {
        label: <IntlMessages id="transactionType" />,
        description: data.cash_flow_type === 1
          ? <IntlMessages id="deposit" />
          : <IntlMessages id="withdrawal" />
      },  
      {
        label: <IntlMessages id="issuerDestination" />,
        description: data.bank_issuer
      },
      {
        label: <IntlMessages id="referenceNo" />,
        description: data.reference_no
      },
      {
        label: <IntlMessages id="note" />,
        description: data.note
      },
    ];

    // const addition = [
    // ];

    // const total = [
    //   {
    //     label: <IntlMessages id="totalCost" />,
    //     amount: this.state.total_cost,
    //     parseAmount: true
    //   },
    //   {
    //     label: <IntlMessages id="totalExpense" />,
    //     amount: data.total_expense,
    //     parseAmount: true
    //   },
    //   {
    //     label: <IntlMessages id="totalAmount" />,
    //     amount: data.total_amount,
    //     parseAmount: true
    //   },
    // ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.cashManagement" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.cash_flow_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.expected_date
                        ? moment(
                            data.expected_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="gLAccountDetails"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={data.cash_management_journals}
                rowKey={row => row.id_cash_management_journal}
                pagination={false}
              />
            </Col>
          </Row>

          {/* <Row type="flex" style={{ marginTop: "24px" }}>
            <Col span={12}>
              <TotalField data={addition} />
            </Col>
            <Col span={12}>
              <TotalField data={total} />
            </Col>
          </Row> */}

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() => document.title = getFileName("cash_management", data.cash_flow_no, true)}
          onAfterPrint={() => document.title = siteConfig.documentTitle}
        />
      </>
    );
  }
}

export default Print;
