import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import CashManagementUpdateForm from "./updateForm";
import CashManagementDocUpdateForm from "./updateDocForm";
import History from "./history";


import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/cashManagement/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import Print from "./print";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/financials/cash-managements";


class UpdateCashManagement extends Component {

  state = {
    visible: false
  }
  // Get All data diawal
  componentDidMount() {
    const { editCashManagement } = this.props;
    const { id } = this.props.match.params;
    editCashManagement(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.cashManagement.error) {
      notification("error", this.props.cashManagement.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.cashManagement.success) {
      notification("success", this.props.cashManagement.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateCashManagement } = this.props;
    const { id } = this.props.match.params;

    updateCashManagement(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelCashManagement } = this.props;
    const { id } = this.props.match.params;

    cancelCashManagement(id, value);
  }

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };

  render() {
    // console.log(this.props);
    const { cash_management, loading } = this.props.cashManagement
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="cashManagements.update.title" />}
            style={{ margin:0 }}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={cash_management} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={cash_management ? cash_management.description : null}
          />

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="cashManagements.tab.detail" />} 
              key="1" 
            >
              {this.props.cashManagement.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <CashManagementUpdateForm
                  submit={this.handleSubmit}
                  cashManagement={this.props.cashManagement}
                  permissions={permissions}
                  processCashManagement={this.props.processCashManagement}
                  handleSubmitCancel={this.handleSubmitCancel}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="cashManagements.tab.attachment" />} 
              key="2"
            >  
             {this.props.cashManagement.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <CashManagementDocUpdateForm
                  submit={this.handleSubmit}
                  cashManagement={this.props.cashManagement}
                  downloadDocument={this.props.downloadDocument}
                />    
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    cashManagement: state.cashManagement, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateCashManagement);
