import React, { Component } from "react";
import moment from "moment"
import EditableTable from "./editableTable";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Icon, Input, Select, Col, InputNumber, DatePicker, Modal, Popconfirm, Steps, Tooltip, Row, Radio, Divider, Empty } from "antd";
import { customDot } from "../../../App/app.style";
import notification from "../../../../components/notification";
import ModalCancel from "./modalCancel";
import { getCurrencyRate } from "../../../../helpers/helper";
import getLanguage from "../../../App/MyLanguange";
// import { deactivateTax } from "../../../../redux/tax/tax/saga";


const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;
const Step = Steps.Step;

class CashManagementUpdateForm extends Component {
  state = {
    currency_is_base: 2,
    clearCashManagementJournalsState: false,
    temp_cash_management_journal: null,
    temp_cash_management_journals: [],
    cash_management_journals: [],
    total_amount: 0,
    total_base_amount: 0,
    currency_rate: null,
    visibleModalCashManagement: false,
    visibleModalCancel: false,
  }

  componentDidUpdate = () => {
    if (this.state.currency_is_base === 2 && this.props.cashManagement.cash_management !== null) {

      const { cash_management } = this.props.cashManagement;
      const { setFieldsValue } = this.props.form;

      setFieldsValue({
        id_location: cash_management.id_location,
        bank_issuer: cash_management.bank_issuer,
        reference_no: cash_management.reference_no,
        id_cash_bank: cash_management.id_cash_bank,
        cash_flow_type: cash_management.cash_flow_type,
        id_currency: cash_management.id_currency,
        currency_rate: parseFloat(cash_management.currency_rate),
        currency_amount: parseFloat(cash_management.currency_amount),
        base_amount: parseFloat(cash_management.currency_rate) * parseFloat(cash_management.currency_amount),
        note: cash_management.note
      })

      let cash_management_journals = [];
      let total_amount = 0;
      let total_base_amount = 0;

      cash_management.cash_management_journals.map((data, key) => {
        let cash_management_journal = {
          account_code: data.account.account_code,
          account_name: data.account.account_name,
          amount: parseFloat(data.amount),
          currency_amount: parseFloat(data.currency_amount),
          description: data.cash_flow_type !== null ? data.cash_flow_type.description : "-",
          id_account: data.id_account,
          id_cash_flow_type: data.id_cash_flow_type,
          id_currency: data.id_currency,
          id_department: data.id_department,
          id_location: data.id_location,
          location_name: data.location.location_name,
          id_project: data.id_project,
          index_no: key + 1,
          id_sub_ledger: data.id_sub_ledger,
          sub_ledger_type: data.sub_ledger_type,
          sub_ledger_name: data.sub_ledger_name,
        }
        cash_management_journals.push(cash_management_journal);
        total_amount += parseFloat(data.currency_amount)
        total_base_amount += parseFloat(data.amount)
      })

      //console.log(cash_management_journals);

      this.setState({
        temp_cash_management_journals: cash_management_journals,
        total_amount,
        total_base_amount,
        currency_rate: cash_management.currency_rate,
        currency_is_base: cash_management.currency.is_base
      })

      this.handleModalCashManagement();
    }
  }

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    })
  }

  handleModalCashManagement = () => {
    // console.log("masuk")
    this.setState({
      visibleModalCashManagement: !this.state.visibleModalCashManagement
    })
  };

  setFieldBaseAmount = (rate) => {
    const { setFieldsValue, getFieldValue } = this.props.form
    let amount = getFieldValue('currency_amount') ? getFieldValue('currency_amount') : 0
    let base_amount = rate * amount;

    setFieldsValue({
      ["currency_rate"]: rate,
      ["base_amount"]: base_amount,
    })
  }

  handleChangeTransDate = (e) => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    if (getFieldValue('expected_date') > getFieldValue('due_date')) {
      setFieldsValue({
        ["due_date"]: null
      })
    }

    if (e instanceof moment) {
      const { getFieldValue } = this.props.form
      if (getFieldValue("id_currency")) {
        this.props.cashManagement.currencies.map((x) => {
          if (x.id_currency === getFieldValue("id_currency")) {
            let rate = getCurrencyRate(x.currency_rate, e)
            this.setFieldBaseAmount(rate)
            //console.log("")
          }
        })
      }
    }

    this.handleChangeBaseAmountFromRate(getFieldValue('currency_rate'))

  }

  handleChangeCashBank = (e) => {
    const { setFieldsValue, getFieldValue } = this.props.form

    this.props.cashManagement.cash_banks.map((data) => {
      if (data.id_cash_bank === e) {
        setFieldsValue({
          ["id_currency"]: data.id_currency,
        })
        this.props.cashManagement.currencies.map((x) => {
          if (x.id_currency === data.id_currency) {
            let rate = getCurrencyRate(x.currency_rate, getFieldValue("expected_date"))
            this.setFieldBaseAmount(rate)
          }
        })
      }
    })

    this.handleChangeBaseAmountFromRate(getFieldValue('currency_rate'))
  }

  handleChangeBaseAmountFromRate = (e) => {
    const { setFieldsValue, getFieldValue } = this.props.form
    let rate = e ? e : 1
    let amount = getFieldValue('currency_amount') ? getFieldValue('currency_amount') : 0
    let base_amount = rate * amount;

    // Kanggo Ngitung Ulang sing ning editable AWOWKOWKOWKOWKO
    let total_amount = 0;
    let total_base_amount = 0;
    let cash_management_journals = [];

    this.state.cash_management_journals.map((data) => {
      data.amount = data.currency_amount * rate
      cash_management_journals.push(data);
      total_amount += data.currency_amount
      total_base_amount += data.amount
    })

    this.setState({
      cash_management_journals,
      total_amount,
      total_base_amount,
      currency_rate: rate
    })

    setFieldsValue({
      ["base_amount"]: base_amount,
    })

  }

  handleChangeBaseAmountFromAmount = (e) => {
    const { setFieldsValue, getFieldValue } = this.props.form
    let rate = getFieldValue('currency_rate') ? getFieldValue('currency_rate') : 0
    let amount = e ? e : 0
    let base_amount = rate * amount;

    setFieldsValue({
      ["base_amount"]: base_amount,
    })
  }

  handleChangeCashFlowType = (e) => {
    const { cash_flow_types } = this.props.cashManagement;

    let cf_types = cash_flow_types.filter((cf_type) => cf_type.transaction_type === e.target.value)

    let deposit = cf_types.find((x) => x.default_cash_management_deposit === 1)
      ? cf_types.find((x) => x.default_cash_management_deposit === 1)
      : cf_types[0]
    let withdrawal = cf_types.find((x) => x.default_cash_management_withdrawal === 1)
      ? cf_types.find((x) => x.default_cash_management_withdrawal === 1)
      : cf_types[0]

    let temp_cash_management_journals = [];
    this.state.cash_management_journals.map((data) => {
      temp_cash_management_journals.push({
        ...data,
        id_cash_flow_type: deposit.transaction_type === 1 ? deposit.id_cash_flow_type : withdrawal.id_cash_flow_type,
        description: deposit.transaction_type === 1 ? deposit.description : withdrawal.description,
      })
    })
    this.setState({
      temp_cash_management_journals
    })
  }

  handleSave = (cash_management_journals, total_amount, total_base_amount) => {
    this.setState({
      cash_management_journals: cash_management_journals,
      total_amount,
      total_base_amount
    });
  };

  clearCashManagementJournal = (cash_management_journals, total_amount, total_base_amount) => {

    this.setState({
      temp_cash_management_journal: null,
      cash_management_journals: cash_management_journals,
      total_amount,
      total_base_amount
    });
  };

  clearCashManagementJournals = (cash_management_journals, total_amount, total_base_amount) => {

    this.setState({
      clearCashManagementJournalsState: false,
      temp_cash_management_journals: [],
      cash_management_journals: cash_management_journals,
      total_amount,
      total_base_amount
    });
  };

  handleChangeGLAccount = (e) => {
    const { getFieldValue } = this.props.form
    const { accounts } = this.props.cashManagement;
    const { customers, suppliers } = this.props.cashManagement
    const [account] = accounts.filter(
      data => data.id_account === e
    );
    let exist = true //indikator apakah punya sub ledger dengan currency yang sama

    // cek apakah sub ledger type supplier, berbeda currency antara cashbank dengan supplier
    if (account.sub_ledger_type === 1) {
      // cari list nya ada tidak 
      const findSup = suppliers.find(data => data.id_location === account.id_location && data.id_account === account.id_account && getFieldValue("id_currency") === data.id_currency)
      // kalau tidak ada
      if (!findSup) {
        exist = false
      }
    }
    // cek apakah sub ledger type customer, berbeda currency antara cashbank dengan 
    else if (account.sub_ledger_type === 2) {
      const findCust = customers.find(data => data.id_account === account.id_account && getFieldValue("id_currency") === data.id_currency)
      // kalau tidak ada
      if (!findCust) {
        exist = false
      }
    }

    if (!exist) {
      Modal.error({
        title: getLanguage({id: "error"}),
        content: getLanguage({id: "accountCannotDifferentCurrency"})
      });
    }
    else {
      this.setState({
        temp_cash_management_journal: account
      });
    }

  }

  handleChangeLocation = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_cash_bank: null
    })
    this.setState({
      clearCashManagementJournalsState: true
    })
  }


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { getFieldValue } = this.props.form;
      const { messages } = this.props.cashManagement
      // console.log(getFieldValue('currency_amount'))
      // console.log(this.state.total_amount);

      if (this.state.cash_management_journals.length < 1) {
        notification("error", messages.gl_account)
      }
      else if (getFieldValue('currency_amount') !== this.state.total_amount) {
        notification("error", messages.unbalance_amount)
      }
      else if (!err) {
        values.expected_date = values.expected_date.format("YYYY-MM-DD HH:mm:ss");
        values.due_date = moment(values.expected_date, 'YYYY-MM-DD').format("YYYY-MM-DD");
        values.cash_management_journals = [];
        this.state.cash_management_journals.map((data) => {
          if (parseFloat(data.currency_amount) > 0) {
            values.cash_management_journals.push(data);
          }
        })
        this.props.submit(values);
      }
    });
  };


  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { cashManagement, processCashManagement } = this.props;
    const { loading } = cashManagement;

    const data = cashManagement.cash_management ? cashManagement.cash_management : [];

    let permissions = this.props.permissions;

    // const error = cashManagement.errorField ? cashManagement.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="cashManagements.add.subtitle.data" />
         </Divider> */}
        <Row style={{ margin: "24px 0px" }} >
          <Col span={16} justify="center">
            <Steps size="small"
              progressDot={customDot}
              current={
                data.status === 4
                  ? 2
                  : data.status === 3
                    ? 3
                    : data.status - 1
              }
            >
              <Step title={getLanguage({id: "open"})} />
              <Step title={getLanguage({id: "processed"})} />
              <Step title={getLanguage({id: "closed"})} />
              <Step title={getLanguage({id: "cancelled"})} />
            </Steps>
          </Col>
        </Row>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div style={{ padding: "0 15px 0 0" }} className="isoColumn-2 isoFormGroupComp">
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="cashManagements.no" />}
            >
              {getFieldDecorator("cash_flow_no", {
                initialValue: data.cash_flow_no,

              })(<Input
                disabled={true}
              />)}
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="purchaseOrders.transactionDate" />
                  }
                >
                  {getFieldDecorator("expected_date", {
                    initialValue: moment(data.expected_date, "YYYY-MM-DD HH:mm:ss"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      onChange={this.handleChangeTransDate}
                      onOpenChange={this.handleChangeTransDate}
                      disabled={
                        permissions.includes("Can Back Date") && data.status === 1
                          ? false
                          : true
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    getFieldValue('id_location') && this.state.cash_management_journals.length > 0 && data.status === 1 ?
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="journals.location.help" />
                        }
                      >
                        <IntlMessages id="locations.pageHeader" />
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                      : <IntlMessages id="locations.pageHeader" />
                  }
                >
                  {getFieldDecorator("id_location", {
                    // initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.cashManagement.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleChangeLocation}
                      disabled={data.status === 1 ? false : true}
                      style={{ width: "100%" }}
                    >
                      {cashManagement.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              {/* <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="purchaseOrders.dueDate" />}
                >
                  {getFieldDecorator("due_date", {
                    initialValue: moment(data.due_date, "YYYY-MM-DD"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      format="DD MMMM YYYY"
                      disabled={data.status === 1 ? false : true}
                      allowClear={false}
                      disabledDate={e => e.valueOf() < getFieldValue('expected_date').subtract(1, 'seconds') }
                    />
                  )}
                </Form.Item>
              </Col> */}
            </InputGroup>

            <InputGroup>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="cashBanks.pageHeader" />}
                >
                  {getFieldDecorator("id_cash_bank", {
                    // initialValue: data.id_cash_bank,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.cashManagement.loading}
                      showSearch
                      // allowClear={true}
                      optionFilterProp="children"
                      onChange={this.handleChangeCashBank}
                      disabled={data.status === 1 ? false : true}
                      notFoundContent={
                        getFieldValue('id_location')
                          ?
                          (
                            <Empty description={<IntlMessages id="noData" />} />
                          )
                          : (
                            <Empty
                              description={
                                <IntlMessages id="noLocation" />
                              }
                            />
                          )
                      }
                      style={{ width: "100%" }}
                    >
                      {getFieldValue('id_location') ?
                        cashManagement.cash_banks.map((data, key) => (
                          getFieldValue('id_location') === data.id_location
                            ?
                            (
                              <Option key={key} value={data.id_cash_bank}>
                                {data.bank_account_number === null
                                  ? data.cash_bank_code
                                  : data.cash_bank_code + " (" + data.bank_account_number + ")"
                                }
                              </Option>
                            )
                            : null
                        ))
                        : null
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="currencies.pageHeader" />}
                >
                  {getFieldDecorator("id_currency", {
                    // initialValue: data.id_currency,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.cashManagement.loading}
                      showSearch
                      optionFilterProp="children"
                      disabled={true}
                      style={{ width: "100%" }}
                    >
                      {cashManagement.currencies.map((data, key) => (
                        <Option key={key} value={data.id_currency}>
                          {data.currency_code + " (" + data.currency_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="rate" />}
                >
                  {getFieldDecorator("currency_rate", {
                    //initialValue: this.state.currency_is_base===1 ? 1 : null,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={0.01}
                      precision={2}
                      disabled={getFieldValue('id_currency')
                        ? cashManagement.currencies.find((cur) => cur.id_currency === getFieldValue('id_currency')).is_base === 1 || data.status !== 1
                          ? true
                          : false
                        : false
                      }
                      //disabled={true}
                      style={{ width: "100%" }}
                      onChange={this.handleChangeBaseAmountFromRate}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="amount" />}
                >
                  {getFieldDecorator("currency_amount", {
                    // initialValue: this.state.currency_is_base===1 ? 1 : null,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={0.01}
                      precision={2}
                      disabled={data.status === 1 ? false : true}
                      style={{ width: "100%" }}
                      onChange={this.handleChangeBaseAmountFromAmount}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="baseAmount" />}
                >
                  {getFieldDecorator("base_amount", {
                    //initialValue: data.base_amount,
                    // initialValue: 0, 
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      //min={0.01}
                      precision={2}
                      disabled={true}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

          </div>
          <div style={{ padding: "0 0 0 15px" }} className="isoColumn-2 isoFormGroupComp">

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="transactionType" />}
            >
              {getFieldDecorator("cash_flow_type", {
                //initialValue: data.cash_flow_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group
                  buttonStyle="solid"
                  onChange={this.handleChangeCashFlowType}
                  disabled={data.status === 1 ? false : true}
                  style={{ display: "flex" }}
                >
                  <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="deposit" /></Radio.Button>
                  <Radio.Button style={{ flex: "1", textAlign: "center" }} value={2}><IntlMessages id="withdrawal" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>


            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="issuerDestination" />}
                >
                  {getFieldDecorator("bank_issuer", {
                    //initialValue: data.bank_issuer,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(<Input
                    disabled={data.status === 1 ? false : true}
                  />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="referenceNo" />}
                >
                  {getFieldDecorator("reference_no", {
                    //initialValue: data.reference_no,

                  })(<Input
                    disabled={data.status === 1 ? false : true}
                  />)}
                </Form.Item>
              </Col>
            </InputGroup>



            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                //initialValue: data.description,
              })(<TextArea
                autosize={{ minRows: 3 }}
                disabled={data.status === 1 ? false : true}
              />)}
            </Form.Item>
          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="cashManagements.add.subtitle.GLAccount" />
        </Divider>
        {
          data.status === 1
            ?
            (
              <Form.Item
                {...formItemLayout}
              //label={<IntlMessages id="GLAccount" />}
              >
                {getFieldDecorator("id_account", {
                  // initialValue: data.id_gl_account,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: <IntlMessages id="required" />
                  //   }
                  // ]
                })(
                  <Select
                    loading={this.props.cashManagement.loading}
                    showSearch
                    optionFilterProp="children"
                    onChange={this.handleChangeGLAccount.bind(this)}
                    notFoundContent={
                      getFieldValue('currency_amount') && getFieldValue('id_cash_bank') && getFieldValue('cash_flow_type')
                        ?
                        (
                          <Empty description={<IntlMessages id="noData" />} />
                        )
                        : (
                          <Empty
                            description={
                              <IntlMessages id="noCashBankAmount" />
                            }
                          />
                        )
                    }
                    style={{ width: "100%" }}
                  // onOpenChange={this.handleChangeGLAccount}
                  // disabled={used ? true : false}
                  >

                    {getFieldValue('id_cash_bank') && getFieldValue('currency_amount') && getFieldValue('cash_flow_type') && getFieldValue('id_location')
                      ?
                      cashManagement.accounts.map((data, key) => {
                        if (data.id_location === getFieldValue('id_location') || !data.id_location) {
                          return (
                            <Option
                              key={key}
                              value={data.id_account}
                            // disabled={
                            //   data.sub_ledger_type === 0
                            //     ? false
                            //     : true
                            // }
                            >
                              {data.account_code + " (" + data.account_name + ")"}
                            </Option>
                          )
                        }
                      })
                      : null
                    }
                  </Select>
                )}
              </Form.Item>
            )
            : null
        }


        <EditableTable
          accounts={this.props.cashManagement.accounts}
          cash_banks={this.props.cashManagement.cash_banks}
          cash_flow_types={
            this.props.cashManagement.cash_flow_types.filter((cf) =>
              cf.transaction_type === getFieldValue('cash_flow_type')
            )
          }
          cash_management_journals={this.state.cash_management_journals}
          clearCashManagementJournal={this.clearCashManagementJournal}
          clearCashManagementJournals={this.clearCashManagementJournals}
          clearCashManagementJournalsState={this.state.clearCashManagementJournalsState}
          currency_rate={getFieldValue('currency_rate')}
          customers={this.props.cashManagement.customers}
          departments={this.props.cashManagement.departments}
          handleSave={this.handleSave}
          id_location={getFieldValue('id_location')}
          locations={this.props.cashManagement.locations}
          projects={this.props.cashManagement.projects}
          status={data.status}
          suppliers={this.props.cashManagement.suppliers}
          temp_cash_management_journal={this.state.temp_cash_management_journal}
          temp_cash_management_journals={this.state.temp_cash_management_journals}
        />


        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginRight: "5px" }}
            loading={loading}
            disabled={
              data.status === 1
                ? false
                : true
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={() =>
              processCashManagement(data.id_cash_management)
            }
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 1 &&
                  permissions.includes("Confirm Cash Management")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Cancel */}

          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 2 &&
                permissions.includes("Cancel Cash Management")
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>

        </Form.Item>
      </Form>
    );
  }
}

const WrappedCashManagementUpdateForm = Form.create({ name: "update_cash_management" })(CashManagementUpdateForm);

export default WrappedCashManagementUpdateForm;
