import React, { Component } from "react";
import { Form, InputNumber, Select } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import NumberFormat from "../../../App/NumberFormat";

const FormItem = Form.Item;
const Option = Select.Option;

class EditableCell extends Component {
  state = {
    editing: false
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) {this.toggleEdit();} else if (editing && this.cell !== e.target && !this.cell.contains(e.target)) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  save = (name, id) => {
    const { record, handleSave, cash_flow_types, locations } = this.props;

    this.form.validateFields((error, values) => {
      if (error) {
        return;
      }

      this.toggleEdit();

      if (name && name === "description") {
        const [cash_flow_type] = cash_flow_types.filter(
          currData => currData.id_cash_flow_type === id
        );
        const newRecord = Object.assign({}, record, {
          id_cash_flow_type: id,
          description: cash_flow_type.description
        });
        handleSave(newRecord);
      } else if (name && name === "location_name") {
        const [location] = locations.filter(
          currData => currData.id_location === id
        );
        const newRecord = Object.assign({}, record, {
          id_location: id,
          location_name: location.location_name
        });
        handleSave(newRecord);
      } else {
        handleSave({ ...record, ...values });
      }
    });
  };

  render() {
    const { editing } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      cash_flow_types,
      locations,
      status,
      ...restProps
    } = this.props;
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (editing && dataIndex === "description") {
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record.id_cash_flow_type,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                    })(
                      <Select
                        ref={node => (this.input = node)}
                        //allowClear
                        showSearch
                        style={{ width: "100%" }}
                        onSelect={this.save.bind(this, "description")}
                        optionFilterProp="children"
                      >
                        {cash_flow_types.map((data, key) => (
                          <Option
                            key={data.id_cash_flow_type}
                            value={data.id_cash_flow_type}
                          >
                            {data.description}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </FormItem>
                );
              } else if (editing && dataIndex === "location_name") {
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record.id_location
                      // rules: [
                      //   {
                      //     required: true,
                      //     message: <IntlMessages id="required" />
                      //   }
                      // ],
                    })(
                      <Select
                        ref={node => (this.input = node)}
                        showSearch
                        style={{ width: "100%" }}
                        onSelect={this.save.bind(this, "location_name")}
                      >
                        {locations.map((data, key) => (
                          <Option
                            key={data.id_location}
                            value={data.id_location}
                          >
                            {data.location_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </FormItem>
                );
              } else if (
                editing &&
                dataIndex === "currency_amount" &&
                status === 1
              ) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        style={{ width: "100%" }}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0.01}
                        precision={2}
                      />
                    )}
                  </FormItem>
                );
              } else if (
                !editing &&
                dataIndex === "currency_amount" &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={record[dataIndex]} />
                      : "0"}
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "currency_amount" &&
                status !== 1
              ) {
                return record[dataIndex]
                  ? <NumberFormat value={record[dataIndex]} />
                  : "0";
              } else if (status !== 1) {
                return restProps.children;
              } else {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
          restProps.children
        )}
      </td>
    );
  }
}

export default EditableCell;
