import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Alert, Form, Input, Select, Col, Row, Modal } from "antd";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
// const { TextArea } = Input;

class ModalDetail extends Component {
  state = {
    idSubLedger: null //simpan id_sub_ledger untuk sub_ledger type 1/2/3
  };

  componentDidUpdate(prevProps) {
    const { selectedData } = this.props;
    const { setFieldsValue } = this.props.form;

    var sub_ledger_type_string = "";
    if (selectedData.sub_ledger_type_string) {
      // data lama
      sub_ledger_type_string = selectedData.sub_ledger_type_string;
    } else {
      // data baru
      if (selectedData.sub_ledger_type === 1) {
        sub_ledger_type_string = "Supplier";
      } else if (selectedData.sub_ledger_type === 2) {
        sub_ledger_type_string = "Customer";
      } else if (selectedData.sub_ledger_type === 3) {
        sub_ledger_type_string = "Cash/Bank";
      }
    }

    if (prevProps.selectedData !== selectedData) {
      // generate id sub ledger ke state
      this.setState({
        idSubLedger: selectedData.id_sub_ledger
          ? selectedData.id_sub_ledger
          : null
      });
      setFieldsValue({
        id_department: selectedData.id_department
          ? selectedData.id_department
          : null,
        id_project: selectedData.id_project ? selectedData.id_project : null,
        //item_note: selectedData.item_note ? selectedData.item_note : null
        sub_ledger_type_string: sub_ledger_type_string,
        id_sub_ledger:
          (selectedData.sub_ledger_type === 1 ||
            selectedData.sub_ledger_type === 2 ||
            selectedData.sub_ledger_type === 3) &&
          selectedData.id_sub_ledger
            ? selectedData.id_sub_ledger
            : null
      });
    }
  }

  handleCancel = () => {
    // const idSubLedger = getFieldValue("id_sub_ledger")
    const { selectedData } = this.props;
    if (
      (selectedData.sub_ledger_type === 1 ||
        selectedData.sub_ledger_type === 2) &&
      !selectedData.id_sub_ledger
    ) {
      this.props.handleDelete(selectedData.index_no);
    }
    this.props.handleDetailModal();
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { cash_banks, customers, selectedData, suppliers } = this.props;
        const subLedgerData = {
          sub_ledger_name: null
        };
        if (values.id_sub_ledger) {
          // mapping sub_ledger_name
          if (selectedData.sub_ledger_type === 1) {
            const findSup = suppliers.find(
              data => data.id_supplier === values.id_sub_ledger
            );
            subLedgerData.sub_ledger_name = findSup.supplier_name;
          } else if (selectedData.sub_ledger_type === 2) {
            const findSup = customers.find(
              data => data.id_customer === values.id_sub_ledger
            );
            subLedgerData.sub_ledger_name = findSup.customer_name;
          } else if (selectedData.sub_ledger_type === 3) {
            const findSup = cash_banks.find(
              data => data.id_cash_bank === values.id_sub_ledger
            );
            subLedgerData.sub_ledger_name = findSup.cash_bank_code;
          }
        }
        subLedgerData.id_sub_ledger = this.state.idSubLedger;

        values.id_department = values.id_department
          ? values.id_department
          : null;
        values.id_project = values.id_project ? values.id_project : null;

        const data = Object.assign({}, selectedData, values, subLedgerData);
        // console.log(data);
        this.props.submit(data);
      }
    });
  };

  generateRules(sub_ledger_type) {
    var arr = [];
    if (sub_ledger_type === 1 || sub_ledger_type === 2) {
      arr.push({
        required: true,
        message: <IntlMessages id="required" />
      });
    }
    return arr;
  }

  handleSelectSubLedger = value => {
    // update id subledger
    this.setState({
      idSubLedger: value
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const {
      cash_banks,
      customers,
      departments,
      projects,
      selectedData,
      suppliers,
      status
    } = this.props;
    // const error = pettyCashType.errorField ? pettyCashType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const modalRestProps = {
      cancelButtonProps: { disabled: false },
      closable: true,
      maskClosable: true
    };
    if (
      (selectedData.sub_ledger_type === 1 ||
        selectedData.sub_ledger_type === 2) &&
      !selectedData.id_sub_ledger
    ) {
      // modalRestProps.cancelButtonProps = { disabled: true }
      modalRestProps.closable = false;
      modalRestProps.maskClosable = false;
    }

    // const formTextArea = {
    //   labelCol: {
    //     xs: { span: 24 },
    //     sm: { span: 24 }
    //   },
    //   wrapperCol: {
    //     xs: { span: 24 },
    //     sm: { span: 24 }
    //   }
    // };

    return (
      <Modal
        {...modalRestProps}
        title={<IntlMessages id="detail" />}
        visible={this.props.visible}
        centered
        onCancel={this.handleCancel}
        onOk={this.handleSubmit}
        width="50%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
      >
        <Row style={{ marginBottom: "15px" }}>
          <Col span={24}>
            {(selectedData.sub_ledger_type === 1 ||
              selectedData.sub_ledger_type === 2) &&
            !selectedData.id_sub_ledger ? (
              <Alert
                message={getLanguage({ id: "clickCancelAddData" })}
                type="warning"
              />
            ) : (
              <Alert
                message={getLanguage({ id: "clickCancelUndoChange" })}
                type="info"
              />
            )}
          </Col>
        </Row>

        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6}>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="subLedgerType" />}
              >
                {getFieldDecorator("sub_ledger_type_string", {
                  //   initialValue: selectedData.id_department
                })(<Input disabled></Input>)}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item {...formItemLayout} label={<IntlMessages id="name" />}>
                {getFieldDecorator("id_sub_ledger", {
                  //   initialValue: selectedData.id_project
                  rules: this.generateRules(selectedData.sub_ledger_type)
                })(
                  <Select
                    showSearch
                    optionFilterProp="children"
                    placeholder={<IntlMessages id="itemRequests.project" />}
                    disabled={
                      status !== 1 ||
                      !selectedData.sub_ledger_type ||
                      selectedData.sub_ledger_type === 3
                        ? true
                        : false
                    }
                    onSelect={this.handleSelectSubLedger}
                  >
                    {selectedData.sub_ledger_type
                      ? selectedData.sub_ledger_type === 1 && suppliers
                        ? // type supplier
                          suppliers.map((data, key) => {
                            if (
                              ((status === 1 &&
                                selectedData.id_account === data.id_account) ||
                                status !== 1) &&
                              selectedData.id_location === data.id_location &&
                              selectedData.id_currency === data.id_currency
                            ) {
                              return (
                                <Option key={key} value={data.id_supplier}>
                                  {data.supplier_code} ({data.supplier_name})
                                </Option>
                              );
                            }
                          })
                        : // type customer
                        selectedData.sub_ledger_type === 2 && customers
                        ? customers.map((data, key) => {
                            if (
                              (((status === 1 &&
                                selectedData.id_account === data.id_account) ||
                                status !== 1) &&
                                selectedData.id_currency ===
                                  data.id_currency) ||
                              data.is_default === 1
                            ) {
                              return (
                                <Option key={key} value={data.id_customer}>
                                  {data.customer_code} ({data.customer_name})
                                </Option>
                              );
                            }
                          })
                        : cash_banks
                        ? // type cashbanks
                          cash_banks.map((data, key) => (
                            <Option key={key} value={data.id_cash_bank}>
                              {data.cash_bank_code}
                            </Option>
                          ))
                        : null
                      : null}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>

          <Row type="flex" justify="start" gutter={6}>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemRequests.department" />}
              >
                {getFieldDecorator("id_department", {
                  //   initialValue: selectedData.id_department
                })(
                  <Select
                    allowClear
                    showSearch
                    optionFilterProp="children"
                    placeholder={<IntlMessages id="itemRequests.department" />}
                    disabled={status !== 1 ? true : false}
                  >
                    {departments.map((data, key) => (
                      <Option key={key} value={data.id_department}>
                        {data.department_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemRequests.project" />}
              >
                {getFieldDecorator("id_project", {
                  //   initialValue: selectedData.id_project
                })(
                  <Select
                    showSearch
                    allowClear
                    optionFilterProp="children"
                    placeholder={<IntlMessages id="itemRequests.project" />}
                    disabled={status !== 1 ? true : false}
                  >
                    {projects.map((data, key) => (
                      <Option key={key} value={data.id_project}>
                        {data.project_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedDetailModal = Form.create({ name: "detail_modal" })(ModalDetail);

export default WrappedDetailModal;
